<template>
  <div class="swiper-wrap">
    <div class="swiper">
      <div id="bannerSwiper" class="banner-swiper">
        <van-swipe ref="swiper" class="swiper-banner" indicator-color="#333" :loop="false" :show-indicators="showIndicator" @change="onChange">
          <van-swipe-item class="swiper-item" :key="image.id" v-for="(image, index) in swiperImg">
            <img v-if="image.baseImage" v-lazy="image.baseImage" @click="fnPreviewImage(index)" class="swiper-img" />
            <div id="swiperVideo" v-else-if="image.video" @click="videoClick(index)">
              <video
                  id="my-player"
                  class="video-js"
                  preload="auto"
                  controls
                  playsinline="true"
                  webkit-playsinline
                  x5-playsinline
                  x-webkit-airplay='allow'
                  x5-video-player-type='h5'
                  x5-video-orientation='portraint'
                  x5-video-player-fullscreen=''
                  :poster="playerOptions.poster">
                  <source :src="playerOptions.sources[0].src" type="video/mp4"/>
                  <p class="vjs-no-js">
                    To view this video please enable JavaScript, and consider upgrading to a
                    web browser that
                    <a href="https://videojs.com/html5-video-support/" target="_blank">
                      supports HTML5 video
                    </a>
                  </p>
              </video>
            </div>
            <img v-else v-lazy="image" @click="fnPreviewImage(index)" class="swiper-img" />
          </van-swipe-item>
        </van-swipe>
      </div>
      <div id="playVideo" :class="{'small-video':smallVideoShow}" @touchstart="touchstart" @touchmove.stop.prevent="touchmove" @touchend="touchend">
        <span class="small-icon" @touchend.stop.prevent="changeVoince">
          <img v-show="isMuted" src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goodsDetail/novioce.png" alt="">
          <img v-show="!isMuted" src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goodsDetail/vioce.png" alt="">
        </span>
        <span class="small-icon small-close" @touchend.stop.prevent="closeSmallVideo">
          <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goodsDetail/close.png" alt="">
        </span>
        <span class="small-icon small-full" @touchend.stop.prevent="smallVideoClick">
          <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goodsDetail/allsceen.png" alt="">
        </span>
        <div id="smallVideoWarp"></div>
        <!-- <video-player ref="refPlayer" customEventName="customstatechangedeventname" :playsinline="true" :options="playerOptions" /> -->
      </div>
      <div class="custom-indicator">
        <span>{{ current + 1 }}</span> / {{swiperImg.length}}
      </div>
    </div>
    <div id="swiper-line"></div>
  </div>
</template>

<script>
// import { ImagePreview } from 'vant'
import 'video.js/dist/video-js.css'
// import commons from '@/utils/commons.js'
import videojs from 'video.js'
// import { videoPlayer } from 'vue-video-player'
import { imageToHttps, imageThumb, imageToWebp } from '@/utils/filters'

let isMoveflag = false // 是否是手势拖动中

export default {
  // components: {
  //   videoPlayer,
  // },
  inject: ['getCurScrollTop'],
  props: ['images', 'products'],
  data() {
    return {
      startX: '',
      offsetX: '',
      offsetY: '',
      oldX: -1, // 上次位置
      oldY: -1, // 上次位置
      player: null, // 播放器

      prevInstance: null,
      current: 0,
      swiperImg: [],
      // videoSlide: false, // 是否banner切换视频
      smallVideoShow: '', // 是否切换小图视频
      fullSceen: false, // 全屏
      isCancelSmallVideo: false, // 是否取消小视频
      isMuted: true, // 是否静音

      showIndicator: true,
      interval: 2000,
      playerOptions: {
        playbackRates: [0.7, 1.0, 1.5, 2.0], // 播放速度
        autoplay: false, // 如果true,浏览器准备好时开始回放。
        muted: true, // 默认情况下将会消除任何音频。
        loop: false, // 导致视频一结束就重新开始。
        preload: this.$util.isIos() ? 'auto' : 'none', // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
        language: 'zh-CN',
        aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
        fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
        sources: [
          {
            type: '', // 这里的种类支持很多种：基本视频格式、直播、流媒体等，具体可以参看git网址项目
            src: '', // url地址
          },
        ],
        poster: '', // 你的封面地址
        // width: document.documentElement.clientWidth, //播放器宽度
        notSupportedMessage: '此视频暂无法播放，请稍后再试', // 允许覆盖Video.js无法播放媒体源时显示的默认信息。
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
          remainingTimeDisplay: false,
          fullscreenToggle: false, // 全屏按钮
          volumePanel: {
            inline: false
          }
        },
      },
    }
  },
  computed: {
    scrollTop() {
      return this.getCurScrollTop()
    },
  },
  mounted() {
    // 视频弹窗关闭
    // let maskVideo = document.getElementById('maskVideo')
    // let maskClose = document.getElementById('maskClose')
    // maskClose.addEventListener('click', () => {
    //   let bannerSwiper = document.getElementById('bannerSwiper')
    //   console.log(bannerSwiper, 'bannerSwiper')
    //   this.fullSceen = !this.fullSceen
    //   bannerSwiper.append(maskVideo.firstElementChild)
    //   maskVideo.classList.remove('active')
    //   this.$emit('sceenChange', this.fullSceen)
    //   this.scrollWatchFn(this.getCurScrollTop())
    //   // 处理安卓自动暂停问题
    //   let ispaused = false
    //   if (this.player && this.player.paused()) {
    //     ispaused = true
    //   } else {
    //     ispaused = false
    //   }
    //   console.log(ispaused, '====ispaused====')
    //   !ispaused && this.player.play()
    // })
    // commons.swipeResize(this, 'swiper')
  },
  watch: {
    scrollTop(newVal) {
      this.scrollWatchFn(newVal)
      if (this.player && this.player !== null) {
        let site = document.getElementById('swiper-line')?.offsetTop;
        let video = document.getElementById('swiperVideo');
        let smallplay = document.getElementById('smallVideoWarp')
        // let site = 100
        // console.log(site, newVal)

        let ispaused = false
        if (this.player && this.player !== null && this.player.paused()) {
          ispaused = true
        } else {
          ispaused = false
        }
        console.log(ispaused, '====ispaused====')

        this.isMuted = this.player.muted()

        if (newVal > site && !this.isCancelSmallVideo) {
          this.smallVideoShow = true
          smallplay.append(video.firstElementChild)
          !ispaused && this.player.play()
        // this.$nextTick(() => {
        //   let winW = window.innerWidth;
        //   let offsetW = document.getElementById('playVideo').offsetWidth
        //   if (this.oldX === -1 || this.oldY === -1) {
        //     this.oldX = winW - offsetW
        //     this.oldY = 100
        //   }
        //   document.getElementById('playVideo').style.left = this.oldX + 'px'
        //   document.getElementById('playVideo').style.top = this.oldY + 'px'
        // })
        } else {
          this.smallVideoShow = false
          video.append(smallplay.firstElementChild)
          !ispaused && this.player.play()
        // document.getElementById('playVideo').style.left = 0
        // document.getElementById('playVideo').style.top = 0
        }
      }
    },
    images: {
      handler(newValue) {
        // if (typeof newValue[0] === 'object') {
        //   this.playerOptions.poster = newValue[0].img.replace('http:', 'https:')
        //   this.playerOptions.sources[0].src = newValue[0].video.replace('http:', 'https:')
        //   if (!this.player) {
        //     this.videoPlugins()
        //   }
        // }

        if (this.checkHasVideo()) {
          this.showIndicator = false
        }
        console.log(newValue, 'newValue')
        this.current = 0
        this.swiperImg = newValue.map((item, index) => {
          let src = imageToHttps(item.img ? item.img : item.baseImage || item)
          src = imageThumb(src, 500)
          src = imageToWebp(src, this.$webpSurpport)
          let swiperItem = {};
          if (item.img) {
            this.swiperVideo = item.video

            this.playerOptions.poster = item.img.replace('http:', 'https:')
            this.playerOptions.sources[0].src = item.video.replace('http:', 'https:')
            // this.player && this.player.dispose()
            this.player = null
            this.videoPlugins()

            swiperItem = {
              id: Math.random(),
              img: src,
              video: item.video,
            }
          } else {
            swiperItem = {
              id: Math.random(),
              src: src,
              loading: 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/img_bg.png',
              error: 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/img_bg.png'
            }
          }
          return swiperItem;
        })
        // console.log(this.swiperImg, 'watch swiperImg')
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    videoClick(index) {
      this.$util.gioTrackWithVariable('MB15620', {
        site_number: index + 1 + '',
        material_type: '视频'
      })
    },
    scrollWatchFn(newVal) {
      if (this.player) {
        let site = document.getElementById('swiper-line')?.offsetTop;
        let video = document.getElementById('swiperVideo');
        let smallplay = document.getElementById('smallVideoWarp')
        // let site = 100
        // console.log(site, newVal)

        let ispaused = false
        if (this.player.paused()) {
          ispaused = true
        } else {
          ispaused = false
        }
        // console.log(ispaused, '====ispaused isCancelSmallVideo====', this.isCancelSmallVideo)

        this.isMuted = this.player.muted()

        if (newVal > site && !this.isCancelSmallVideo) {
          this.smallVideoShow = true
          smallplay.append(video.firstElementChild)
          !ispaused && this.player.play()
        // this.$nextTick(() => {
        //   let winW = window.innerWidth;
        //   let offsetW = document.getElementById('playVideo').offsetWidth
        //   if (this.oldX === -1 || this.oldY === -1) {
        //     this.oldX = winW - offsetW
        //     this.oldY = 100
        //   }
        //   document.getElementById('playVideo').style.left = this.oldX + 'px'
        //   document.getElementById('playVideo').style.top = this.oldY + 'px'
        // })
        } else {
          this.smallVideoShow = false
          video.append(smallplay.firstElementChild)
          !ispaused && this.player.play()
        // document.getElementById('playVideo').style.left = 0
        // document.getElementById('playVideo').style.top = 0
        }
      }
    },

    touchstart(ev) {
      // console.log(ev, 'ev')
      if (this.smallVideoShow) {
        let startX = ev.touches[0].pageX;
        let startY = ev.touches[0].pageY;
        let offsetX = document.getElementById('playVideo').offsetLeft
        let offsetY = document.getElementById('playVideo').offsetTop;
        this.offsetX = startX - offsetX
        this.offsetY = startY - offsetY
      }
      // else {
      //   this.startX = ev.touches[0].pageX;
      // }
    },

    touchmove(ev) {
      isMoveflag = true
      if (this.smallVideoShow) {
        let moveX = ev.touches[0].pageX;
        let moveY = ev.touches[0].pageY;
        let left, top;
        let winW = window.innerWidth;
        let winH = window.innerHeight;
        let offsetH = document.getElementById('playVideo').offsetHeight
        let offsetW = document.getElementById('playVideo').offsetWidth
        if (moveX - this.offsetX > winW - offsetW) {
          left = winW - offsetW
        } else {
          left = (moveX - this.offsetX < 0) ? 0 : (moveX - this.offsetX)
        }
        if (moveY - this.offsetY < 0) {
          top = 50;
        } else {
          top = (moveY - this.offsetY > winH - offsetH) ? (winH - offsetH - 100) : moveY - this.offsetY
        }
        this.oldX = left;
        this.oldY = top
        document.getElementById('playVideo').style.left = left + 'px';
        document.getElementById('playVideo').style.top = top + 'px';
      }
      // else {
      //   let moveX = ev.touches[0].pageX;
      //   // console.log(this.startX - moveX)
      //   if (this.startX - moveX > 100 && this.swiperImg.length > 1) {
      //     console.log('swiper right')
      //     this.videoSlide = true
      //     document.getElementById('playVideo').style.top = 0
      //   }
      //   if (this.startX - moveX < -100 && this.current === 0) {
      //     console.log('swiper left')
      //     this.videoSlide = false
      //   }
      // }
    },
    touchend() {
      if (!isMoveflag) return
      isMoveflag = false
      let winH = window.innerHeight;
      let winW = window.innerWidth;
      let offsetW = document.getElementById('playVideo').offsetWidth
      let offsetH = document.getElementById('playVideo').offsetHeight
      this.offsetX = ''
      this.offsetY = ''
      this.startX = ''
      if (this.oldX > (winW - offsetW) / 2) {
        document.getElementById('playVideo').style.left = (winW - offsetW) + 'px';
      } else {
        document.getElementById('playVideo').style.left = 0 + 'px';
      }

      if (this.oldY < 50) {
        document.getElementById('playVideo').style.top = 50 + 'px';
      } else if (this.oldY > (winH - offsetH - 100)) {
        document.getElementById('playVideo').style.top = winH - offsetH - 100 + 'px';
      }
    },

    onChange(index) {
      this.current = index
      console.log(this.current, 'index+++++++')
      if (this.checkHasVideo() && index === 0) {
        this.showIndicator = false
      } else {
        this.showIndicator = true
      }
      this.$emit('swiperChange', index)
    },
    checkHasVideo() {
      let flag = false;
      if (this.products.videoUrl) {
        flag = true
      }
      return flag
    },
    fnPreviewImage(index) {
      this.$util.gioTrackWithVariable('MB15620', {
        site_number: index + 1 + '',
        material_type: '图片'
      })
      this.fullSceen = !this.fullSceen
      // 处理安卓自动暂停问题
      let ispaused = false
      if (this.player && this.player.paused()) {
        ispaused = true
      } else {
        ispaused = false
      }
      console.log(ispaused, '====ispaused====')
      if (this.fullSceen) {
        let maskVideo = document.getElementById('maskVideo')
        let bannerSwiper = document.getElementById('bannerSwiper');
        console.log(maskVideo, 'maskVideo')
        maskVideo.append(bannerSwiper.firstElementChild)
        maskVideo.classList.add('active')
        this.$emit('sceenChange', this.fullSceen)
        !ispaused && this.player && this.player.play()
      } else {
        let maskVideo = document.getElementById('maskVideo')
        let bannerSwiper = document.getElementById('bannerSwiper')
        console.log(bannerSwiper, 'bannerSwiper')
        bannerSwiper.append(maskVideo.firstElementChild)
        maskVideo.classList.remove('active')
        this.$emit('sceenChange', this.fullSceen)
        !ispaused && this.player && this.player.play()
      }
    },

    videoPlugins() {
      let that = this
      let Button = videojs.getComponent('Button');
      let fullBtn = videojs.extend(Button, {
        constructor: function (player, options) {
          Button.call(this, player, options);
          this.setAttribute('id', 'fullBtn');
        },
        handleClick: function (options) {
          console.log(options) // { name: 'customName' }
          that.fullSceen = !that.fullSceen

          // 处理安卓自动暂停问题
          let ispaused = false
          if (that.player && that.player.paused()) {
            ispaused = true
          } else {
            ispaused = false
          }
          console.log(ispaused, '====ispaused====')

          if (that.fullSceen) {
            let maskVideo = document.getElementById('maskVideo')
            let bannerSwiper = document.getElementById('bannerSwiper');
            console.log(maskVideo, 'maskVideo')
            maskVideo.append(bannerSwiper.firstElementChild)
            maskVideo.classList.add('active')
            that.$emit('sceenChange', that.fullSceen)
            !ispaused && that.player.play()
          } else {
            let maskVideo = document.getElementById('maskVideo')
            let bannerSwiper = document.getElementById('bannerSwiper')
            console.log(bannerSwiper, 'bannerSwiper')
            bannerSwiper.append(maskVideo.firstElementChild)
            maskVideo.classList.remove('active')
            that.$emit('sceenChange', that.fullSceen)
            !ispaused && that.player.play()
            that.scrollWatchFn(that.getCurScrollTop())
          }
        },
        buildCSSClass: function () {
          return 'vjs-custom-control vjs-control vjs-button-full';
        }
      });
      videojs.registerComponent('fullBtn', fullBtn);

      this.$nextTick(() => {
        this.player = videojs('my-player', this.playerOptions, () => {
          console.log(this.player, 'videoplayer')
          this.player.getChild('controlBar').addChild('fullBtn', {}, 10);
        })

        this.player.on('play', () => {
          console.log('playing')
          this.$util.gioTrackWithVariable('MB15620', {
            site_number: '1',
            material_type: '视频'
          })
          this.$util.isWifi().then(isWifi => {
            if (!isWifi) {
              this.$toast('当前非Wifi播放，请注意流量消耗')
            }
          })
          this.isCancelSmallVideo = false
        })

        this.$util.isWifi().then(isWifi => {
          if (isWifi) {
            console.log('isWifi')
            this.player.muted(this.isMuted)
            this.player.play()
          } else {
            console.log('not isWifi')
          }
        })

        // this.$refs.refPlayer.player.getChild('controlBar').addChild('PrevButton', {}, 0);
      })
    },

    // 切换音量
    changeVoince() {
      this.isMuted = !this.isMuted
      this.player.muted(this.isMuted)
    },

    // 关闭小窗
    closeSmallVideo() {
      let video = document.getElementById('swiperVideo');
      let smallplay = document.getElementById('smallVideoWarp');
      this.smallVideoShow = false
      this.isCancelSmallVideo = true
      video.append(smallplay.firstElementChild)
      if (this.player && !this.player.paused()) {
        this.player.pause()
      }
    },

    smallVideoClick() {
      console.log('smallVideoClick')
      // 处理安卓自动暂停问题
      let ispaused = false
      if (this.player && this.player.paused()) {
        ispaused = true
      } else {
        ispaused = false
      }
      console.log(ispaused, '====ispaused====')
      // if (!ispaused) {
      this.closeSmallVideo()
      this.isCancelSmallVideo = false
      let maskVideo = document.getElementById('maskVideo')
      let bannerSwiper = document.getElementById('bannerSwiper');
      console.log(maskVideo, 'maskVideo')
      maskVideo.append(bannerSwiper.firstElementChild)
      maskVideo.classList.add('active')
      this.fullSceen = true
      this.$emit('sceenChange', true)
      !ispaused && this.player.play()
      // }
    }

  },
  beforeDestroy() {
    if (this.prevInstance) this.prevInstance.close()
  }
}
</script>
