<style lang="scss" scoped>
#swiperVideo{
  font-size: 0;
  overflow: hidden;
  height: 7.5rem;
}
#playVideo{
  height: 1.76rem;
  font-size: 0;
  overflow: hidden;
}
#smallVideoWarp{
  position: absolute;
  top: 0;
  left: 0;
  width: 3.12rem;
  height: 1.76rem;
  z-index: 2;
}

.swiper-wrap {
  position: relative;
  width: 7.5rem;
  background-color: #fff;
}

.swiper{
  position: relative;
  width: 7.5rem;
  height: 7.5rem;
